<template>
  <TheTwoRowLayout :fullsize="true">
    <template #topRow>
      <div class="col-10 col-lg-4">
        <div class="display-5">Flugzeug</div>
        <hr />
        <div class="display-1">Quiz</div>
      </div>
    </template>
    <template #bottumRow>
      <div class="col-10 col-lg-5">
        <img
          src="@/assets/p47thunderbolt.svg"
          alt="Flugzeug"
          class="img-fluid"
        />
      </div>
      <div class="col-10 col-lg-3">
        <div class="mb-5">
          <span>Jetzt Namen eingeben und los gehts.</span>
          <form class="mt-4" @submit="startQuiz">
            <input
              type="text"
              name="nickname"
              id="nickname"
              class="form-control text-center"
              placeholder="Nickname"
              v-model="name"
            />
            <div class="d-grid mt-2">
              <button class="btn btn-primary">Start</button>
            </div>
          </form>
        </div>
      </div>
    </template>
  </TheTwoRowLayout>
</template>

<script>
import TheTwoRowLayout from "@/layouts/TheTwoRowLayout.vue";
import Store from "../store";

export default {
  name: "StartComponent",
  components: {
    TheTwoRowLayout,
  },
  data() {
    return {
      name: "",
    };
  },
  methods: {
    startQuiz(e) {
      e.preventDefault();
      Store.mutations.setUserName(this.name);
      this.$router.push({ path: "/welcome" });
    },
  },
};
</script>

